.div2{
  float: left;
  width: 55%;
  height: 100%;
  .titleNode {
    width: 384px;
    height: 120px;
    color: #ffffff;
    /* float: left; */
    line-height: 103px;
    text-align: center;
    cursor: pointer;
    position: relative;
    /* margin-left: 88px; */
    font-size: 44px;
    text-shadow: 0 0 30px rgb(244, 250, 245);
  }
  .realtime{
    width: 1742px;
    height: 723px;
    margin-top: 29px;
    .demo {
      overflow: hidden; height: 500px;
    }
    .demo2 {
      margin-top: -20px;
    }
  }

  .smalltable{ width:1685px;
    overflow: hidden;
    .table{ width: 1685px;
      padding-top: 50px;
      padding-left: 60px;
      margin: 74px 0 0 36px;
      tr{height: 90px}
      th{
        //display: inline-block;
        height: 40px;
        font-family: MicrosoftYaHei;
        font-size: 40px;
        font-weight: bold;
        font-stretch: normal;
        line-height: 40px;
        letter-spacing: 2px;
        color: #ffffff;}
       
      td{
        height: 40px;
        font-family: MicrosoftYaHei;
        font-size: 40px;
        font-stretch: normal;
        line-height: 40px;
        letter-spacing: 2px;
        color: #ffffff;
        text-align: center;
      }
      td:nth-child(1){
        width: 303px;
      }
      td:nth-child(2){
        width: 192px;
      }
      td:nth-child(3){
        width: 178px;
      }
      td:nth-child(4){
        width: 98px;
      }
      td:nth-child(5){
        width: 153px;
      }
      td:nth-child(6){
        width: 153px;
      }
      td:nth-child(7){
        width: 304px;
      }
      td:nth-child(8){
        width: 304px;
      }
      .striped1 {
        background-color: #172362;
      }
      .striped {
        background-color: #0a5bb2;
        text-align: center;
      }
      .striped2 {
        background-color: #133884;
      }
    }
  }
}
