.div2 {
  float: left;
  width: 55%;
  height: 100%;
}
.div2 .titleNode {
  width: 384px;
  height: 120px;
  color: #ffffff;
  /* float: left; */
  line-height: 103px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 44px;
  text-shadow: 0 0 30px #f4faf5;
}
.div2 .realtime {
  width: 1742px;
  height: 723px;
  margin-top: 29px;
}
.div2 .realtime .demo {
  overflow: hidden;
  height: 500px;
}
.div2 .realtime .demo2 {
  margin-top: -20px;
}
.div2 .smalltable {
  width: 1685px;
  overflow: hidden;
}
.div2 .smalltable .table {
  width: 1685px;
  padding-top: 50px;
  padding-left: 60px;
  margin: 74px 0 0 36px;
}
.div2 .smalltable .table tr {
  height: 90px;
}
.div2 .smalltable .table th {
  height: 40px;
  font-family: MicrosoftYaHei;
  font-size: 40px;
  font-weight: bold;
  font-stretch: normal;
  line-height: 40px;
  letter-spacing: 2px;
  color: #ffffff;
}
.div2 .smalltable .table td {
  height: 40px;
  font-family: MicrosoftYaHei;
  font-size: 40px;
  font-stretch: normal;
  line-height: 40px;
  letter-spacing: 2px;
  color: #ffffff;
  text-align: center;
}
.div2 .smalltable .table td:nth-child(1) {
  width: 303px;
}
.div2 .smalltable .table td:nth-child(2) {
  width: 192px;
}
.div2 .smalltable .table td:nth-child(3) {
  width: 178px;
}
.div2 .smalltable .table td:nth-child(4) {
  width: 98px;
}
.div2 .smalltable .table td:nth-child(5) {
  width: 153px;
}
.div2 .smalltable .table td:nth-child(6) {
  width: 153px;
}
.div2 .smalltable .table td:nth-child(7) {
  width: 304px;
}
.div2 .smalltable .table td:nth-child(8) {
  width: 304px;
}
.div2 .smalltable .table .striped1 {
  background-color: #172362;
}
.div2 .smalltable .table .striped {
  background-color: #0a5bb2;
  text-align: center;
}
.div2 .smalltable .table .striped2 {
  background-color: #133884;
}
